<template>
  <v-container fluid fill-height class="login-container">
    <v-row align="center" justify="center" class="fill-height">
      <v-col cols="12" sm="10" md="8" lg="6" xl="4">
        <!-- Main Login Card -->
        <v-card class="login-card elevation-12" rounded="lg">
          <!-- Header Section -->
          <div class="login-header">
            <div class="text-center pa-8">
              <v-avatar size="80" class="mb-4" color="primary">
                <v-icon size="40" color="white">mdi-account-group</v-icon>
              </v-avatar>
              <h1 class="text-h4 font-weight-bold mb-2">Welcome Back</h1>
              <p class="text-body-1 text--secondary">
                Sign in to your Task Management account
              </p>
            </div>
          </div>

          <!-- Login Form -->
          <v-card-text class="pa-8">
            <v-form ref="form" v-model="valid" @submit.prevent="handleLogin">
              <!-- Email Field -->
              <v-text-field
                v-model="form.email"
                label="Email Address"
                name="email"
                prepend-inner-icon="mdi-email-outline"
                type="email"
                :rules="emailRules"
                :error-messages="emailErrors"
                outlined
                rounded
                class="mb-4"
                required
                autocomplete="email"
                @input="clearFieldError('email')"
              ></v-text-field>

              <!-- Password Field -->
              <v-text-field
                v-model="form.password"
                label="Password"
                name="password"
                prepend-inner-icon="mdi-lock-outline"
                :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                :type="showPassword ? 'text' : 'password'"
                :rules="passwordRules"
                :error-messages="passwordErrors"
                outlined
                rounded
                class="mb-2"
                required
                autocomplete="current-password"
                @click:append="showPassword = !showPassword"
                @input="clearFieldError('password')"
                @keyup.enter="handleLogin"
              ></v-text-field>

              <!-- Remember Me & Forgot Password -->
              <div class="d-flex justify-space-between align-center mb-6">
                <v-checkbox
                  v-model="form.rememberMe"
                  label="Remember me"
                  color="primary"
                  hide-details
                  dense
                ></v-checkbox>
                <v-btn
                  text
                  color="primary"
                  small
                  @click="showForgotPassword = true"
                >
                  Forgot Password?
                </v-btn>
              </div>

              <!-- Login Button -->
              <v-btn
                type="submit"
                color="primary"
                :loading="loading"
                :disabled="!valid || loading"
                block
                large
                rounded
                class="mb-4"
                elevation="2"
              >
                <v-icon left>mdi-login</v-icon>
                Sign In
              </v-btn>
            </v-form>

            <!-- Divider -->
            <div class="text-center my-6">
              <v-divider></v-divider>
              <span class="text-body-2 text--secondary px-4 bg-surface">
                or continue with
              </span>
            </div>

            <!-- Social Login Buttons -->
            <div class="d-flex flex-column gap-3">
              <v-btn
                color="white"
                :loading="googleLoading"
                :disabled="loading"
                block
                large
                rounded
                elevation="1"
                @click="handleGoogleLogin"
              >
                <v-icon left color="red">mdi-google</v-icon>
                Continue with Google
              </v-btn>

              <v-btn
                color="success"
                :loading="demoLoading"
                :disabled="loading || googleLoading"
                block
                large
                rounded
                elevation="1"
                @click="handleDemoLogin"
              >
                <v-icon left>mdi-account-circle</v-icon>
                Try Demo Account
              </v-btn>
            </div>
          </v-card-text>

          <!-- Footer -->
          <v-card-actions class="pa-8 pt-0">
            <div class="text-center w-100">
              <p class="text-body-2 text--secondary mb-2">
                Don't have an account?
              </p>
              <v-btn
                color="primary"
                text
                large
                to="/register"
                class="font-weight-medium"
              >
                Create Account
              </v-btn>
            </div>
          </v-card-actions>
        </v-card>

        <!-- Error Alert -->
        <v-alert
          v-if="error"
          type="error"
          class="mt-4"
          dismissible
          @input="clearError"
          rounded="lg"
        >
          <v-icon left>mdi-alert-circle</v-icon>
          {{ error }}
        </v-alert>

        <!-- Success Alert -->
        <v-alert
          v-if="successMessage"
          type="success"
          class="mt-4"
          dismissible
          @input="successMessage = ''"
          rounded="lg"
        >
          <v-icon left>mdi-check-circle</v-icon>
          {{ successMessage }}
        </v-alert>
      </v-col>
    </v-row>

    <!-- Forgot Password Dialog -->
    <v-dialog v-model="showForgotPassword" max-width="400" persistent>
      <v-card rounded="lg">
        <v-card-title class="text-h5">
          <v-icon left>mdi-lock-reset</v-icon>
          Reset Password
        </v-card-title>
        <v-card-text>
          <p class="text-body-2 mb-4">
            Enter your email address and we'll send you a link to reset your
            password.
          </p>
          <v-text-field
            v-model="resetEmail"
            label="Email Address"
            prepend-inner-icon="mdi-email-outline"
            type="email"
            :rules="emailRules"
            outlined
            rounded
            required
          ></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="showForgotPassword = false">Cancel</v-btn>
          <v-btn
            color="primary"
            :loading="resetLoading"
            @click="handlePasswordReset"
          >
            Send Reset Link
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { auth } from '@/firebase/config'

export default {
  name: 'Login',
  data() {
    return {
      valid: false,
      showPassword: false,
      showForgotPassword: false,
      resetEmail: '',
      resetLoading: false,
      googleLoading: false,
      demoLoading: false,
      successMessage: '',
      form: {
        email: '',
        password: '',
        rememberMe: false
      },
      emailErrors: [],
      passwordErrors: [],
      emailRules: [
        v => !!v || 'Email is required',
        v => /.+@.+\..+/.test(v) || 'Email must be valid'
      ],
      passwordRules: [
        v => !!v || 'Password is required',
        v => v.length >= 6 || 'Password must be at least 6 characters'
      ]
    }
  },
  computed: {
    ...mapGetters('auth', ['loading', 'error'])
  },
  mounted() {
    // Check if user is already logged in
    if (this.$store.getters['auth/isAuthenticated']) {
      this.$router.push('/')
    }

    // Load saved email if remember me was checked
    const savedEmail = localStorage.getItem('rememberedEmail')
    if (savedEmail) {
      this.form.email = savedEmail
      this.form.rememberMe = true
    }
  },
  methods: {
    ...mapActions('auth', ['signIn', 'signInWithGoogle', 'clearError']),

    async handleLogin() {
      if (!this.$refs.form.validate()) return

      try {
        await this.signIn(this.form)

        // Handle remember me
        if (this.form.rememberMe) {
          localStorage.setItem('rememberedEmail', this.form.email)
        } else {
          localStorage.removeItem('rememberedEmail')
        }

        this.$toast.success('Welcome back!')
        this.$router.push('/')
      } catch (error) {
        this.handleLoginError(error)
      }
    },

    async handleGoogleLogin() {
      this.googleLoading = true
      try {
        await this.signInWithGoogle()
        this.$toast.success('Welcome!')
        this.$router.push('/')
      } catch (error) {
        this.$toast.error('Google login failed: ' + error.message)
      } finally {
        this.googleLoading = false
      }
    },

    async handleDemoLogin() {
      this.demoLoading = true
      try {
        // Create a demo user session
        const demoUser = {
          uid: 'demo-user-' + Date.now(),
          email: 'demo@taskmanagement.app',
          displayName: 'Demo User',
          photoURL: 'https://i.pravatar.cc/150?img=3',
          isDemo: true
        }

        // Set demo user in store
        this.$store.commit('auth/SET_USER', demoUser)
        this.$store.commit('auth/SET_LOADING', false)

        // Store demo session
        localStorage.setItem('demoSession', JSON.stringify(demoUser))

        this.$toast.success(
          'Demo login successful! Explore the app with sample data.'
        )
        this.$router.push('/')
      } catch (error) {
        this.$toast.error('Demo login failed: ' + error.message)
      } finally {
        this.demoLoading = false
      }
    },

    async handlePasswordReset() {
      if (!this.resetEmail || !/.+@.+\..+/.test(this.resetEmail)) {
        this.$toast.error('Please enter a valid email address')
        return
      }

      this.resetLoading = true
      try {
        await auth.sendPasswordResetEmail(this.resetEmail)
        this.showForgotPassword = false
        this.resetEmail = ''
        this.successMessage = 'Password reset email sent! Check your inbox.'
        this.$toast.success('Password reset email sent!')
      } catch (error) {
        this.$toast.error('Failed to send reset email: ' + error.message)
      } finally {
        this.resetLoading = false
      }
    },

    handleLoginError(error) {
      const errorCode = error.code
      const errorMessage = error.message

      // Clear previous field errors
      this.emailErrors = []
      this.passwordErrors = []

      // Handle specific Firebase auth errors
      switch (errorCode) {
        case 'auth/user-not-found':
          this.emailErrors = ['No account found with this email address']
          break
        case 'auth/wrong-password':
          this.passwordErrors = ['Incorrect password']
          break
        case 'auth/invalid-email':
          this.emailErrors = ['Invalid email address']
          break
        case 'auth/user-disabled':
          this.$toast.error('This account has been disabled')
          break
        case 'auth/too-many-requests':
          this.$toast.error('Too many failed attempts. Please try again later.')
          break
        case 'auth/network-request-failed':
          this.$toast.error('Network error. Please check your connection.')
          break
        default:
          this.$toast.error('Login failed: ' + errorMessage)
      }
    },

    clearError() {
      this.$store.dispatch('auth/clearError')
    },

    clearFieldError(field) {
      if (field === 'email') {
        this.emailErrors = []
      } else if (field === 'password') {
        this.passwordErrors = []
      }
    }
  }
}
</script>

<style scoped>
.login-container {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
}

.login-card {
  backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.95) !important;
}

.theme--dark .login-card {
  background: rgba(30, 30, 30, 0.95) !important;
}

.login-header {
  background: linear-gradient(135deg, #1976d2 0%, #1565c0 100%);
  color: white;
  border-radius: 8px 8px 0 0;
}

.gap-3 {
  gap: 12px;
}

/* Responsive adjustments */
@media (max-width: 600px) {
  .login-container {
    padding: 16px;
  }

  .login-card {
    margin: 0;
  }
}

/* Animation for form elements */
.v-text-field {
  transition: all 0.3s ease;
}

.v-btn {
  transition: all 0.3s ease;
}

.v-btn:hover {
  transform: translateY(-1px);
}

/* Focus styles */
.v-text-field--outlined:not(.v-input--is-focused)
  .v-input__control
  .v-input__slot {
  border-color: rgba(0, 0, 0, 0.12);
}

.v-text-field--outlined.v-input--is-focused .v-input__control .v-input__slot {
  border-color: #1976d2;
  border-width: 2px;
}
</style>
